.bio {
  position: absolute;
  height: 270px;
  width: 600px;
  /* background-color: rgba(7, 7, 7, 0.9); */
  /* background-color: #7c7b7b; */
  z-index: 99999;
  background-color: #aeaeae;
  /* border: 1px solid #333; */
  padding-bottom: 30px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .7);
  overflow: scroll;
}
.overlay{
  position: absolute;
  top:0;
  left:0;
  width:100svw;
  height: 100svh;
  background-color: rgba(4, 4, 4, .9);
  display: flex;
  justify-content: center;
  align-items: center;
}
.actionbar {
  position: sticky;
  top:0;
  left: 0;
  width: 100%;
  height: 40px;
  display: flex;
  gap: 15px;
  justify-content: flex-start;
  align-items: center;
  margin: 0 auto;
  padding:0 20px;
  background-color: #aeaeae;
  border: 1px solid rgba(98, 97, 97, .5);
}
.actionbarcontent {
  width: 50px;
  height: 100%;
}
.action {
  width: 17px;
  height: 17px;
  border: none;
  padding-bottom:5px;
  border-radius: 50%;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .1);
  opacity: .7;
  cursor: pointer;


}
.action:hover{
  opacity: 1;
}

.action:nth-child(1){
  background-color: red;
}
.action:nth-child(2){
  background-color:yellow;
}
.action:nth-child(3){
  background-color:green;
}
.heading{
  margin-top: 20px;
  padding-left: 30px;
}
.heading h1{
  font-size: 1.1rem;
  font-weight: 500;
  text-shadow: 0 0 1px black;
  letter-spacing: .1rem;
}
.heading p{
  font-size: .8rem;
  color:#7a0303;
  text-shadow: 0 0 1px #7a0303;
  letter-spacing: .1rem;
  font-weight: 300;
}

.paragraph{
  display: flex;
  flex-direction: column;
  font-size: .9rem;
  gap:10px;
  margin-top: 30px;
  padding-left: 50px;
  width:90%;
  letter-spacing: .1rem;
  /* text-shadow: 0 0 1px #4019ef; */
  color:#4019ef;
  font-weight: 500;
  line-height: 1.6;

  
}
.minimize{
  width:2px;
  height:5px;
}

@media (max-width:620px){
  .bio{
    width: 400px;
  }
}

@media (max-width:420px){
  .bio{
    width: 350px;
  }
}
