.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100svw;
  height: 100svh;
  background-color: rgba(13, 13, 13, 0.7);
  z-index: 1;
}

.banner-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100svw;
  height: 100svh;
  background-image: url("../../assets/math.gif");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banner-intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  color: transparent;
  position: relative;
  z-index: 2;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: rgb(243, 238, 238);
  text-transform: uppercase;
  /* mix-blend-mode:multiply; */

}



.hello span {
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: rgb(241, 11, 11);
  text-transform: uppercase;
}

.know-me {
  width: fit-content;
  position: relative;
  background-color: transparent;
  padding: 12px 15px;
  letter-spacing: 0.4rem;
  cursor: pointer;
  text-transform: uppercase;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 1px 1px 1px rgba(81, 79, 79, 0.7);
  z-index: 2;
  color: rgba(235, 232, 232);
  text-shadow: 0 0 1px rgb(237, 236, 236);
}

.know-me::after {
  content: "Know me";
  position: absolute;
  width: calc(100% + 1px);
  height: calc(100% + 1px);
  top: 0px;
  background-color: rgb(88, 89, 89);
  left: -150px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 500ms ease-in;
  z-index: 100;
  color:red;
  text-shadow: 0 0 1px rgb(238, 5, 5);
}

.know-me:hover.know-me::after {
  left: 0;
}

